* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  list-style-type: none;
  text-decoration: none;
  scroll-behavior: smooth;
}

body {
  font-family: 'Roboto Slab', serif;
}

.header {
  width: 100%;
  height: 100vh;
  background: url("../public/images/img1.jpg");
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: cover;
  position: relative;
}

.content {
  padding-top: 12%;
  margin-left: 55px;
  width: 45%;
}

.header_text {
  display: flex;
  flex-direction: column;
  color: white;
  gap: 25px;
}

.header_text h3 {
  font-size: 50px;
}

.header_text h1 {
  font-size: 80px;
  font-weight: 900;
}

.navbar_img {
  width: 30px;
}

.navbar {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
}

.navbar_data {
  display: flex;
  height: 50px;
  justify-content: space-between;
}
.navbar_list{
  margin-left: auto;
}

.total_data{
  max-width: 50%;
  display: flex;
  gap:20px;
  margin-left: auto;
  margin-right: auto;
  align-items: center;
  border-bottom: 2px solid rgb(255, 149, 0);
}
.datas{
  display: flex;
  flex-direction: column;
  gap:10px;
  align-items: center;
}
.total_data h3{
  width: 200px;
}
.head_total{
  display: inline-block;
  font-size: 40px;
  margin-left: 40%;
  border-bottom: 2px solid rgb(255, 149, 0);
}
.final_data{
  display: flex;
  max-width: 40%;
  margin-left: auto;
  margin-right: auto;
  flex-direction: column;
 padding-left: 155px;
  margin-top: 30px;
  color: rgb(128, 125, 125);
}

.final_data .sp1{
  margin-left: 50px;
}
.final_data .sp2{
  margin-left: 105px;
}
.count_total{
  margin-top: 30px;
  height: 80px;
  width: 220px;
  text-align: center;
  padding-top: 28px;
  border-radius: 30px;
  font-size: 20px;
  font-weight: 800;
  background-color: rgb(255, 149, 0);
  color: white;
}
.res{
  margin-top: 50px;
  margin-left: 20px;
 text-align: center;
 font-size: 20px;
 font-weight: 600;
}
.image{
  width: 250px;
  height: 250px;
}
.menu_icon{
  height: 25px;
  width: 18px;
}
.menu_icon .bx{
  font-size: 30px;
  color: white;
}
.btn_data{
  display: flex;
  margin-top: 20px;
  gap:10px;
  color: white;
  width: 100px;
  height: 25px;
}
.btn_data .btns{
  width: 30px;
  background-color: rgb(255, 149, 0);
  color: white;
  text-align: center;
  border: 0px;
  border-radius: 50%;
  margin-left: 2px;
  margin-right: 2px;
}
.hidden{
  display: none;
}

.lists {
  display: flex;
  gap: 50px;
  align-items: center;
  height: 45px;
  margin-right: 30px;
}

.lists li a{
  
  color: white;
  font-weight: 500;
  text-transform: uppercase;
}
.lists li a:hover{
  cursor: pointer;
  border-bottom: 2px solid rgb(255, 149, 0);
}

.btn {
  width: 170px;
  height: 50px;
  border-radius: 25px;
  background-color: rgb(255, 149, 0);
  text-align: center;
  padding-top: 12px;
}
.btn:hover{
  cursor: pointer;
  box-shadow: 10px 10px 5px rgba(rgb(174, 173, 173, 0.8));
}

.btn a {
  color: white;
  font-size: 18px;
}

.about_content {
  padding-bottom: 140px;
  display: flex;
  flex-direction: column;
  gap: 15px
}

.about_content h3 {
  color: grey;
  font-size: 25px;
  font-weight: 500;
}

.about_content h2 {
  font-size: 45px;
  font-weight: 700;
}

.about_content p {
  color: rgb(166, 164, 164);
}

.about_container {
  display: flex;
  gap: 10px
}

.about_image{
  padding-top: 140px;
  padding-left: 20px;
  padding-bottom: 140px;
}

.about_image img {
  height: 350px;
  width: 550px;
}

.shop_content{
  width: 100%;
  height: 500px;
  background: url("../public/images/img3.jpeg");
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: cover;
}

.shop_cont{
  width: 450px;
  line-height: 30px;
  padding-top:15px;
  color: white;
  margin-left: auto;
  margin-right: auto;
  text-align: center;
}
.shop_btn{
  margin-top: 20px;
  margin-left: auto;
  margin-right: auto;
}
.shop_btn a{
  justify-content: center;
}

.menu__container{
  display: flex;
  gap:10px;
  margin-top: 120px;
  margin-left: 50px;
  margin-right: 50px;
  padding-left: 80px;
  margin-bottom: 120px;
}
.menu_image img{
  width: 350px;
  height: 300px;
}
.menu__container .container{
  display: flex;
  flex-direction: column;
  width: 550px;
  gap:15px;
  margin-left: 40px;
  
}
.menu__container .container h3 {
  color: grey;
  font-size: 25px;
  font-weight: 500;
}

.menu__container .container h2 {
  font-size: 45px;
  font-weight: 700;
}

.menu__container .container p {
  color: rgb(166, 164, 164);
}
.container_data{
  width:270px;
  display: flex;
  flex-direction: column;
  text-align: center;
  gap:10px;
}
.menu_heading{
  text-align: center;
  font-size: 20px;
  margin-bottom: 50px;
}
.Prices_content{
  max-width: 1050px;
  display: flex;
  flex-basis: 33.333333%;
  flex-wrap: wrap;
  gap:80px;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 100px;
}
.price_image img{
  width:300px;
  height: 250px;
  border-radius: 40px;
}
.container_data h3{
  font-size: 20px;
  font-weight: 800;
}
.container_data p{
  color: rgb(166, 164, 164);
}
.price_btn button{
  height: 40px;
  width:80px;
  border-radius: 40px;
  border: 0;
  font-size: 15px;
  font-weight: 600;
  background-color: rgb(255, 149, 0);
  color: white;
}
.price_btn button:hover{
opacity: 0.6;
cursor: pointer;
}


.banner_data{
  bottom:0;
  left:0;
  position: fixed;
  background-color: rgb(255, 149, 0);
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-left: 120px;
  padding-right: 100px;
  color: white;
  font-size: 20px;
  height: 40px;
  width: 100%;
}
.bxs-trash-alt:hover{
  cursor: pointer;
}
.banner_data a{
  color: white;
  font-weight: 600;
}
.contact_content{
  width:100%;
  height:200px;
  background: url("/public/images/burger_final.jpg");
  background-attachment: fixed;
  background-repeat: no-repeat;
  background-size: cover;
}
.contact_data{
  display: flex;
  flex-direction: column;
  gap:30px;
  align-items: center;
}
.contact_data h4{
  font-size: 30px;
  color: white;
}
.logos{
  display: flex;
  gap:20px;
  font-size: 50px;
  color: white;
}
.logos .bx:hover{
  color: rgb(255, 149, 0);
  cursor: pointer;
}
.btn-bill{
  width: 90px;
  height: 35px;
  background-color: white;
  color: rgb(255, 149, 0);
  border:0;
  margin-top: 5px;
  margin-bottom: 5px;
  border-radius: 17px;
  font-size: 15px;
  font-weight: 700;

}
.btn-bill:hover{
  cursor: pointer;
  opacity:0.8;
}

